.Main{
    color:lightcyan;
    font-size:large;
    font-family: cursive;
    text-align: center;
}
.Jumbotron{
    background-color:firebrick;
}
.profile{
    background-color:firebrick;
}
#rotate{
    animation: rotation 6s infinite linear;
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-top: 4ch;
    margin-bottom: 4ch;
    width: 50%;
}
@keyframes rotation{
    from {
        transform: rotate(0deg);
      }
      to {
        transform: rotate(359deg);
      }
}
.card{
    background-color:firebrick;
    margin-top: 1cm;
    margin-bottom: 1cm;
}
.image{
    margin-left: auto;
    margin-right: auto;
    margin-top: 2mm;
    width: min-content;
}
.button{
    width: 8.5cm;
}
a:link,a:visited, h2{
    color:palegoldenrod
}
